.wrapper {
  width: 100vw;
  position: absolute;
  top: 0;
  bottom: 0;
}

.background {
  background: rgba(0, 0, 0, 0.15);
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
}

.popup {
  background-color: #fff;
  border-radius: 20px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -ms-border-radius: 20px;
  -o-border-radius: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: absolute;
  top:50%;
  left:50%;
  transform:translate(-50%, -50%);
  padding: 30px;
  gap: 30px;
}

.popup__img {
  height: 160px;
  width: 273px;
  position: relative;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
}

.popup__img::before {
  content: '';
  position: absolute;
  z-index: 0;
}

.popup__img::after {
  content: '';
  position: absolute;
  background-repeat: no-repeat;
  background-position: center;
}

.remove .popup {
  width: 323px;
}

.err .popup {
  width: 516px;
  height: 277px;
  top: 49.7%;
}

.remove .popup__img {
  width: 80px;
  height: 80px;
  background-color: #FFE9E5;
}

.remove .popup__img::before {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  background-color: #FF4A33;
  left: 10px;
  top: 10px;
}

.remove .popup__img::after {
  width: 20px;
  height: 36px;
  background-image: url('../../assets/icons/delete_white.svg');
  top: 30px;
  left: 30px;
}

.success .popup__img {
  width: 80px;
  height: 80px;
  background-color: #E5FFE5;
}

.success .popup__img::before {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  background-color: #5CCC81;
  left: 16px;
  top: 14px;
}

.success .popup__img::after {
  width: 20px;
  height: 36px;
  background-image: url('../../assets/icons/succes.svg');
  top: 20px;
  left: 30px;
}

.err .popup__img::after {
  width: 273px;
  height: 160px;
  background-image: url('../../assets/img/error.png');
  top: 0;
  left: 0;
}

.popup__text {
  display: flex;
  flex-direction: column;
  gap: 4px;
  align-items: center;
  justify-content: center;
}

.popup__subtitle {
  text-align: center;
  font-size: 14px;
  margin: 0;
}

.popup__title {
  text-align: center;
  font-size: 20px;
  margin: 0;
}

.popup__btns {
  height: 44px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.popup__button {
  padding: 15px 10px;
  flex: 1;
  letter-spacing: 0.1em;
  transition: all .3s linear;
  -webkit-transition: all .3s linear;
  -moz-transition: all .3s linear;
  -ms-transition: all .3s linear;
  -o-transition: all .3s linear;
}

.popup__button:hover {
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.3);
}

.remove .popup__button:first-child {
  background-color: #FF4A33;
  color: #fff;
}

.remove .popup__button:last-child {
  background-color: #FFE9E5;
  color: #E5432E;
}

.success .popup__btns{
  width: 85%;
}

.err .popup__btns{
  width: 50%;
}

.popup__button_close {
  background-color: #F2F4F7;
  color: #42474D;
}

.popup__button_error {
  background-color: #4DA2D6;
  color: #fff;
}